import {
	mapActions,
	mapState,
	mapGetters
} from 'vuex';
import {
	uniPopup,
	// uniPopupDialog
} from '@dcloudio/uni-ui'
export default {
	components: {
		uniPopup,
		// uniPopupDialog
	},
	data() {
		return {
			input:""
		}
	},
	computed: {
		...mapGetters(['openid', 'hasUser'])
	},
	methods: {
		change(e) {
			console.log('popup ' + e.type + ' 状态', e.show)
		},
		dialogInputConfirm(done, val) {
			uni.showLoading({
				title: '3秒后会关闭'
			})
			console.log(val);
			this.value = val
			setTimeout(() => {
				uni.hideLoading()
				// 关闭窗口后，恢复默认内容
				done()
			}, 3000)
		},
		confirmDialog() {
			this.$refs.dialogInput.open()
		},
	}
}
